.link-wrap {
  max-width: 1000px;
  justify-content: space-between;
  margin: auto auto 100px;
  padding-top: 30px;
  padding-left: 40px;
  display: flex;
}

.date-btn-wrap {
  max-width: 1300px;
  justify-content: space-between;
  margin: auto auto 100px;
  display: flex;
}

#datetime-picker {
  min-width: 900px;
  font-size: large;
}

button {
  cursor: pointer;
  min-width: 300px;
  min-height: 60px;
  color: #11121c;
  transition-property: background-color, color, box-shadow;
  transition-duration: var(--time-duration);
  transition-timing-function: var(--timing-function);
  background: #eaeaea;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 48px;
  display: block;
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button:-webkit-any(:focus, :hover) {
  color: #fff;
  background-color: rgba(54, 55, 55, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button:is(:focus, :hover) {
  color: #fff;
  background-color: rgba(54, 55, 55, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

button:active {
  color: #e4e059;
  background-color: rgba(26, 26, 26, .94);
  box-shadow: 0 2px 6px rgba(46, 47, 66, .08), 0 2px 2px rgba(46, 47, 66, .16), 0 2px 3px rgba(46, 47, 66, .08);
}

.timer {
  max-width: 1300px;
  flex-direction: row;
  justify-content: space-between;
  margin: auto;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  color: gold;
  font-size: 150px;
  font-weight: 700;
}

.label {
  color: #c8c8c8;
  font-size: 80px;
}

